import PropTypes from "prop-types"
import React, { useState, useEffect } from "react"
import Axios from "../../axios"
import { BASE_URL } from "../../config"
import btnArrow from "../images/home/btn-arrow.svg"
import btnArrow1 from "../images/home/btn-arrow-1.svg"
import BackgroundImage from "../images/home/slider-bg.png"
import { Location } from "@reach/router"
import Carousel from "react-bootstrap/Carousel"

import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"


const query = graphql`
    {
      allStrapiHeroImages {
        nodes {
          home {
            localFile {
              childImageSharp {
                original {
                  src
                }
              }
            }
          }
        }
      }

    }
`


function Header({ isHomePage = true, title = "Industrial Services" }) {

  const data = useStaticQuery(query)
  const heroImages = data.allStrapiHeroImages.nodes[0]


  // const [HeroImages, setHeroImages] = useState({})
  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/hero-images")
  //     setHeroImages(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])

  return (
    <section id="bs_carousel" className="carousel slide" data-ride="carousel">
      <Carousel>
        {heroImages?.home?.map((document, i) => (
          <Carousel.Item>
            <img
              width="100%"
              // src={`${BASE_URL}${
              //   HeroImages?.home ? HeroImages.home[i]?.url : ""
              // }`}
              src={document.localFile.childImageSharp?.original.src}
              // HeroImages?.home ? HeroImages.home[i]?.formats?.medium?.url : ""
              className="img-fluid"
              alt="Los Angeles"
            />
            <div className="carousel-caption">
              {/* <button className="btn-best-insustry">BEST INDUSTRY </button> */}
              <h2 className="h-best">We Provide Best</h2>
              <h1 className="h-ind-services">Industrial Solutions</h1>
              <div class="color-button">
              <Link to="/#industry">
                <button className="btn btn-white  mt-4">
                  Our Partners <img src={btnArrow1} className="ml-1" />
                </button>
              </Link>
              <Link to="/#services">
                <button className="btn btn-msar ml-3 mt-4">
                  Our Services <img src={btnArrow1} className="ml-1" height />
                </button>
              </Link>
              <Link to="/contact">
                <button className="btn btn-white ml-3 mt-4">
                  Contact Us <img src={btnArrow1} className="ml-1" />
                </button>
              </Link>
              </div>
            </div>
          </Carousel.Item>
        ))}
      </Carousel>

      <a
        className="carousel-control-prev"
        href="#bs_carousel"
        data-slide="prev"
      >
        <span className="carousel-control-prev-icon" />
      </a>
      <a
        className="carousel-control-next"
        href="#bs_carousel"
        data-slide="next"
      >
        <span className="carousel-control-next-icon" />
      </a>
    </section>
  )
}

Header.propTypes = {}

export default Header
